import * as jsPDF from 'jspdf';

import html2canvas from 'html2canvas';
export function exportPdf(elementName, fileName, isClass) {
  global.html2canvas = html2canvas; // html2canvas is not a function

  let element = document.getElementById(elementName); // dompdfdiv
  if (isClass) {
    element = document.getElementsByClassName(elementName)[0];
  }
  let w = element.offsetWidth; // 
  let h = element.offsetHeight; // 
  let offsetTop = element.scrollTop; // 
  let offsetLeft = element.scrollLeft; // 
  let canvas = document.createElement('canvas');
  let abs = 0;
  let winI = window.offsetWidth; // 
  let winO = window.scrollWidth; // 
  if (winO > winI) {
    abs = (winO - winI) / 2; // 
  }
  canvas.width = w * 2; // &&
  canvas.height = h * 2;
  let context = canvas.getContext('2d');
  context.scale(2, 2);
  context.translate(-offsetLeft - abs, -offsetTop);
  // offset.left(),
  // translate
  console.log(element, 'html2canvas');
  html2canvas(element).then(function (canvas) {
    var contentWidth = canvas.width;
    var contentHeight = canvas.height;
    // pdfhtmlcanvas;
    var pageHeight = (contentWidth / 592.28) * 841.89;
    // pdfhtml
    var leftHeight = contentHeight;
    // 
    var position = 0;
    // a4[595.28,841.89]htmlcanvaspdf
    var imgWidth = 595.28;
    var imgHeight = (592.28 / contentWidth) * contentHeight;
    var pageData = canvas.toDataURL('image/jpeg', 1.0);
    // eslint-disable-next-line
    let pdf = new jsPDF();
    // htmlpdf(841.89)
    // pdf
    if (leftHeight < pageHeight) {
      pdf.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight);
    } else {
      // 
      while (leftHeight > 0) {
        pdf.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight);
        leftHeight -= pageHeight;
        position -= 841.89;
        // 
        if (leftHeight > 0) {
          pdf.addPage();
        }
      }
    }
    if (!fileName) {
      fileName = 'PDF';
    }
    pdf.save(fileName + '.pdf');
  });
}



